import React from 'react';
import '../Styles/Biography.css'; // Import your CSS file for styling

function Biography() {
  return (
    <div className="biography-container" id='bio'>
      <h1>Biography of Mahatma Gandhi</h1>
      <div className="biography-section">
        <h2>Early Life and Education:</h2>
        <p>
          Mohandas Karamchand Gandhi was born on October 2, 1869, in Porbandar, Gujarat, India. He was born into a devout Hindu family and received his early education in Porbandar and later in Rajkot. Gandhi was married to Kasturba Makhanji at the age of 13 and had four children.
        </p>
      </div>
      <div className="biography-section">
        <h2>Legal Career in South Africa:</h2>
        <p>
          Gandhi moved to South Africa in 1893 to work as a lawyer. He faced racial discrimination firsthand in South Africa, which led him to become an advocate for civil rights. Gandhi developed his concept of Satyagraha (truth force) during his time in South Africa, which later became central to his philosophy of nonviolent resistance.
        </p>
      </div>
      <div className="biography-section">
        <h2>Return to India:</h2>
        <p>
          After spending over two decades in South Africa, Gandhi returned to India in 1915. He became involved in the Indian independence movement, advocating for the rights of Indians under British rule.
        </p>
      </div>
      <div className="biography-section">
        <h2>Role in Indian Independence Movement:</h2>
        <p>
          Gandhi emerged as a leader in the Indian independence movement, advocating for nonviolent resistance against British rule. He led numerous peaceful protests, including the Salt March and Quit India Movement, which played a significant role in India's eventual independence in 1947.
        </p>
      </div>
      <div className="biography-section">
        <h2>Philosophy of Satyagraha:</h2>
        <p>
          Gandhi's philosophy of Satyagraha, or truth force, emphasized the power of nonviolent resistance to achieve social and political change. He believed in the moral superiority of nonviolence and used it as a tool to confront injustice and oppression.
        </p>
      </div>
      <div className="biography-section">
        <h2>Principles of Ahimsa:</h2>
        <p>
          Ahimsa, or nonviolence, was central to Gandhi's philosophy and way of life. He believed in the inherent dignity and worth of every individual and practiced nonviolence in all aspects of his life, including his interactions with others and his approach to conflict resolution.
        </p>
      </div>
      <div className="biography-section">
        <h2>Challenges and Controversies:</h2>
        <p>
          Gandhi faced numerous challenges and controversies throughout his life, including criticism from various quarters for his methods and decisions. He also faced personal struggles and doubts but remained committed to his principles of truth and nonviolence.
        </p>
      </div>
      <div className="biography-section">
        <h2>Partition of India:</h2>
        <p>
          The partition of India in 1947 into India and Pakistan led to widespread violence and displacement. Gandhi opposed the partition and worked tirelessly to promote peace and reconciliation between Hindus and Muslims, but his efforts were met with challenges and ultimately, he was unable to prevent the bloodshed.
        </p>
      </div>
      <div className="biography-section">
        <h2>Personal Life and Beliefs:</h2>
        <p>
          Gandhi led a simple and austere life, practicing self-discipline and frugality. He believed in the importance of living in accordance with one's values and principles and was known for his vegetarianism, celibacy, and commitment to truth and nonviolence.
        </p>
      </div>
      <div className="biography-section">
        <h2>Legacy and Influence:</h2>
        <p>
          Gandhi's legacy continues to inspire millions of people around the world. His teachings on nonviolence, truth, and moral courage have influenced numerous social and political movements, including the civil rights movement in the United States and the anti-apartheid movement in South Africa.
        </p>
      </div>
      {/* Add more sections as needed */}
    </div>
  );
}

export default Biography;
