import React, { useState, useEffect } from 'react';
import '../Styles/Books.css';

function Books() {
  const [books, setBooks] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('https://www.googleapis.com/books/v1/volumes?q=gandhi');
        if (!response.ok) {
          throw new Error('Failed to fetch data');
        }
        const data = await response.json();
        setBooks(data.items);
      } catch (error) {
        console.error('Error fetching books:', error);
      }
    };

    fetchData();
  }, []);

  const handleViewBook = (bookTitle) => {
    const wikipediaUrl = `https://en.wikipedia.org/wiki/${bookTitle.replace(/ /g, '_')}`;
    window.open(wikipediaUrl, '_blank');
  };

  return (
    <div className="books-container" id='books'>
      <div className="books-container1">
        <h1>Books</h1>
      </div>
      <div className="books-grid">
        {books.map((book, index) => (
          <div className="book-card" key={index}>
            <h3 className="book-title">{book.volumeInfo.title}</h3>
            {book.volumeInfo.imageLinks && (
              <img src={book.volumeInfo.imageLinks.thumbnail} alt={book.volumeInfo.title} className="book-cover" />
            )}
            <p className="book-description">{book.volumeInfo.description}</p>
            <button className="view-book-btn" onClick={() => handleViewBook(book.volumeInfo.title)}>View Book</button>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Books;
