import React from 'react';
import '../Styles/TributeInfo.css'; // Import your CSS file for styling

function TributeInfo1() {
  
  return (
    <div className="tribute-info-container1" id="tribute-info-container"> {/* Ensure id matches href in Navbar */}

      <h2>Tribute Information</h2>
      <div className="details">
        <p>
          Mohandas Karamchand Gandhi, commonly known as Mahatma Gandhi, was born
          on October 2, 1869, in Porbandar, Gujarat, India. He played a pivotal
          role in India's struggle for independence from British rule through
          nonviolent civil disobedience. Gandhi's philosophy of satyagraha
          (truth-force) and ahimsa (nonviolence) inspired movements for civil
          rights and freedom across the world. He led numerous campaigns and
          protests, including the Salt March and Quit India Movement, to achieve
          social and political reforms in India. Gandhi's life and teachings
          continue to influence and inspire people around the globe.
        </p>
        <p>
          Gandhi led India to independence and inspired movements for civil rights and freedom across the world. The honorific Mahatma (Sanskrit: "great-souled", "venerable"), first applied to him in 1914 in South Africa, is now used throughout the world.
        </p>
        <p>
          Gandhi's activism as well as his deep spirituality, led to India's independence from British rule in 1947. His philosophy of nonviolence, known as Satyagraha (Sanskrit: "truth-force"), had a profound impact on the world, including the American civil rights movement led by Martin Luther King Jr.
        </p>
        <h3>Early Life</h3>
        <p>
          Gandhi was born and raised in a Hindu family in coastal Gujarat and trained in law at the Inner Temple, London. He first employed nonviolent civil disobedience while an expatriate lawyer in South Africa during the resident Indian community's struggle for civil rights.
        </p>
        <p>
          In India, Gandhi led nationwide campaigns for various social causes and for achieving Swaraj (self-rule). Gandhi famously led Indians in challenging the British-imposed salt tax with the 400 km (250 mi) Dandi Salt March in 1930, and later in calling for the British to Quit India in 1942.
        </p>
        <h3>Assassination</h3>
        <p>
          Gandhi was assassinated in 1948 by Nathuram Godse, a Hindu nationalist who opposed Gandhi's tolerance for Muslims, as well as his adherence to nonviolence.
        </p>
        <h3>Legacy</h3>
        <p>
          Mahatma Gandhi's legacy extends far beyond India's independence. His
          principles of nonviolence, tolerance, and peaceful resistance have
          inspired countless movements for civil rights, social justice, and
          freedom around the world. He remains a symbol of hope and inspiration
          for people fighting against oppression and injustice everywhere.
        </p>
        <h3>Inspiration to Future Generations</h3>
        <p>
          Gandhi's life and teachings continue to resonate with people of all
          ages and backgrounds. His commitment to truth, nonviolence, and
          simplicity serves as a guiding light for those striving for a better
          world. His message of love, compassion, and unity reminds us of the
          power of individual action and the importance of standing up for what
          is right, even in the face of adversity.
        </p>
        <h3>International Influence</h3>
        <p>
          Gandhi's influence extends beyond the borders of India, inspiring
          leaders and activists around the world. Figures such as Martin Luther
          King Jr., Nelson Mandela, and Cesar Chavez drew inspiration from
          Gandhi's principles of nonviolent resistance in their own struggles
          for justice and equality. His legacy continues to inspire movements
          for peace, human rights, and social change across the globe.
        </p>
      </div>
      <div className="gallery">
        <h3>Image Gallery</h3>
        <div className="image-container">
          <img src="https://www.deccanchronicle.com/h-upload/2024/01/30/1073408-img-20240130-wa0063.webp" alt="Gandhi Image 1" />
          <img src="https://cdn.magzter.com/1574404609/1706653423/articles/oCooKFlxOXzci9TXCwPsys/GUV-CM-PAY-TRIBUTE-TO-GANDHI-ON-HIS-DEATH-ANNIV.jpg" alt="Gandhi Image 2" />
          <img src="https://thesamikhsya.com/wp-content/uploads/2022/06/Rome21-768x496.jpg" alt="Gandhi Image 3" />
          {/* Add more images as needed */}
        </div>
      </div>
      <div className="video-section">
        <h1>For More Information Watch Videos</h1>
        <iframe
  width="960px"
  height="315"
  src="https://www.youtube.com/embed/8chKMqmP-Ys"
  title="Gandhi Video 1"
  frameBorder="0"
  allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
  allowFullScreen
  style={{ border: "none", borderRadius: "5px",  width:"1150px"}} // Apply inline CSS here
></iframe>

      </div>
    </div>
  );
}

export default TributeInfo1;
